"use client";

import { useState } from "react";
import api from "@/utils/api";
import Image from "next/image";
import Container from "@/src/components/Container";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [msg, setMsg] = useState("");

  const lidaComLogin = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const res = await api.post("/login", { email, password });
      localStorage.setItem("token", res.data.token);
      setMsg("Login executado");
    } catch {
      setMsg("Erro ao fazer Login");
    }
  };

  return (
   <Container>
     <div className="min-h-screen flex items-center justify-center bg-[#0d1117] px-4">
      <div className="w-full max-w-sm text-center">

        {/* Logo */}
        <div className="flex justify-center mb-6">
          <Image
                               src="/logo-1.png"
                               alt="Logo AVB"
                               width={140}
                               height={40}
                           />

          
        </div>

        <h1 className="text-white text-2xl font-semibold mb-6">
          Sign in to GitHub
        </h1>

        {/* Form estilizado */}
        <form
          onSubmit={lidaComLogin}
          className="bg-[#161b22] p-6 rounded-md border border-[#30363d] text-left"
        >
          <label className="text-sm font-medium text-gray-300">Email</label>
          <input
            type="email"
            placeholder="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="mt-1 w-full bg-[#0d1117] border border-[#30363d] rounded-md px-3 py-2 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
          />

          <div className="flex justify-between items-center mt-4">
            <label className="text-sm font-medium text-gray-300">
              Password
            </label>
            <a
              href="#"
              className="text-sm text-blue-500 hover:underline"
            >
              Forgot password?
            </a>
          </div>

          <input
            type="password"
            placeholder="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="mt-1 w-full bg-[#0d1117] border border-[#30363d] rounded-md px-3 py-2 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
          />

          <button
            type="submit"
            className="mt-6 w-full bg-[#238636] hover:bg-[#2ea043] text-white py-2 rounded-md font-semibold"
          >
            Login
          </button>
        </form>

        {/* Mensagem */}
        {msg && (
          <p className="text-gray-300 text-sm mt-4">
            {msg}
          </p>
        )}
      </div>
    </div>
  
   </Container>
  )
}
